*{
    box-sizing: border-box;
}
.homeDiv{
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 90vh;
    font-family: 'Francois One', sans-serif;
}
.homeSpan{
    color: #9d4edd;
}
.homeCVButton{
    text-decoration: none;
    color: #9d4edd;
    /* color: white; */
    border: 1px solid #9d4edd;
    background-color: white;
    /* background-image: linear-gradient(to right, #e0aaff, #c77dff ); */
    font-family: 'Francois One', sans-serif;
    width: fit-content;
    height: fit-content;
    padding: 0.5rem;
    font-size: 20px;
}
.homeCVButton:hover{
    /* background-color: #9d4edd; */
    background-image: linear-gradient(to right, #9d4edd, #7b2cbf );
    color: white;
   
    cursor: pointer;
}
.homeContentDiv{
    width: 50%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin-top: 7rem;
}
.homeHeading{
    text-align: left;
    font-size: 50px;
}
.homeIntro{
    text-align: left;
    font-size: 40px;
}
.homeImgDiv{
    width: 50%;
    height: 100%;
    margin-top: 7rem;
}
.homeImgDiv>img{
    width: 100%;
    height: 100%;
    object-fit: contain;
}